.side-bar {
  max-height: calc(100vh - 185px);
  overflow: auto !important;
}
.card-active {
  border-color: rgba(249, 87, 94, 0.5) !important;
  cursor: pointer;
}
.caption {
  font-size: 12px;
}
.header-widget {
  padding: 0 4px;
  position: relative;
  top: -16px;
  left: -4px;
  width: calc(100% + 32px);
  background-color: #40a9ff;
  border-radius: 16px;
  height: 64px;
}
.sentre-select-selector {
  color: #e9e9eb !important;
  width: 151px !important;
  background-color: transparent !important;
  border: solid 1px #e9e9eb !important;
  box-shadow: none !important;
}
.sentre-select-selector:hover .sentre-select-selector:focus {
  border: solid 1px #e9e9eb !important;
}
.sentre-select-arrow {
  color: #e9e9eb !important;
}
.sentre-select-selection-item {
  color: #e9e9eb !important;
}
.expandHidden {
  display: none;
}
.sentre-collapse-header {
  display: none !important;
}
.sentre-collapse-content-box {
  padding: 0 !important;
}
.deposit-tag {
  position: relative;
  background: #f9575e1a !important;
  color: #f9575e !important;
  border-radius: 4px !important;
  border: none !important;
}
.deposit-tag::before {
  content: '';
  position: absolute;
  width: 0;
  height: 0;
  top: 50%;
  transform: translateY(-50%);
  left: -4px;
  border-top: 4px solid transparent;
  border-bottom: 4px solid transparent;
  border-right: 4px solid #f9575e1a;
}

#light #sen_lp .content {
  color: black;
}

#dark #sen_lp .content {
  color: white;
}

